<template>
  <div v-show="message != ''">
    <div class="msg" id="msg">
      <div class="msg_text" id="msg_text">
      </div>
    </div>
    <div style="position: absolute; bottom: 30px; right: 10px;">
      <img :src="getImgUrl(this.emoji)" alt="" height="90px">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Actor',
  props: {
    message: String,
    emoji: String,
    delay: Number
  },
  data() {
    return {
      show_continue: false
    }
  },
  watch: {
    message(v) {
      if (v == null || v == undefined) return;
      this.typeWriter();
    }
  },
  methods: {
    timeout(delay) {
      return new Promise(resolve => setTimeout(resolve, delay));
    },
    async typeWriter() {
      console.log(this.message.length)
      const elem = document.getElementById("msg_text");
      elem.innerHTML = '';
      for (const letter of this.message) {
        elem.innerHTML += letter;
        if (letter == ' ') continue;
        await this.timeout(90);
      }
      setTimeout(() => {
        this.show_continue = true;
        elem.innerHTML += '<div style="font-size: 12px; color: #969696">Нажми для продолжения</div>';
      }, 0)
    },
    getImgUrl(img) {
        if (img == '' || img == null) return null;

        const images = require.context('../assets/', false, /\.png$/);
        return images('./' + img + ".png");
      },
  }
}
</script>

<style scoped>

.msg {
  background-color: aliceblue;
  border-radius: 50px;
  min-height: 6em;
  width: 50%;
  padding: 16px;
  position: absolute;
  bottom: 134px;
  right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.msg_text {
  text-align: center;
  vertical-align: middle;
}

</style>