<template>
  <div style="height: calc(100vh - 130px);">
    <a-scene embedded arjs
      v-if="status != 'begin' 
      && status != 'scan_baloons' 
      && status != 'scan_baloons_dlg1' 
      && status != 'scan_models_dlg1'
      && status != 'labirint'
      && status != 'taskphotos'
      && status != 'taskphotos_dlg2'
      && status != 'labirint_dlg2'
      && status != 'end'"
    >
      <a-marker id="cake" preset="hiro">
        <a-gltf-model position="0 -1 0" scale="1.1 1.1 1.1" src="cake2.gltf" animation-mixer></a-gltf-model>
      </a-marker>
      <a-marker id="strawberry" type="pattern" url="strawberry.patt">
      </a-marker>
      <a-entity camera></a-entity>
    </a-scene>
    <div v-show="status == 'end'" style="margin-top: 90px; display: flex; justify-content: center;" class="animation_end">
      <img :src="getPngUrl('birthday')" style="width: 100vw;">
    </div>
    <ScanBaloons v-if="status == 'scan_baloons'" @completed="scanBaloonsComplete"/>
    <Actor :message="current_msg" :emoji="current_emoji" :delay="current_delay"/>
  </div>
</template>

<script>
import Actor from './Actor.vue';
import ScanBaloons from './ScanBaloons.vue';

function stop(event) {
  event.currentTarget.acontroller.abort();
}

export default {
  name: 'Arjs',
  components: {
    Actor,
    ScanBaloons
  },
  data() {
    return {
      status: 'begin', // begin -> scan_baloons_dlg1 -> scan_baloons -> scan_models_dlg1 -> scan_models -> end
      current_msg: '',
      current_emoji: '',
      current_delay: 0,
      status_msgs: {
        'begin': [
          {
            text: 'С днем рождения, Любимая!!!',
            delay: 2420,
            emoji: 'max_4',
          },
          {
            text: 'Сегодня у тебя праздник. Проходи в комнату, тебя ждет первое задание.',
            delay: 5800,
            emoji: 'max_1',
          },
          {
            text: 'Тебе нужно будет составить слово из букв на шарах. Слово это подскажет путь истинный.',
            delay: 7050,
            emoji: 'max_1'
          },
          {
            text: 'Удачи!',
            delay: 550,
            emoji: 'max_3',
            next_status: 'scan_baloons_dlg1'
          }
        ],
        'scan_baloons_dlg1': [
          {
            text: 'После составления слов тебе нужно будет сфотографировать его для определения корректности.',
            delay: 7950,
            emoji: 'max_1',
          },
          {
            text: 'Просто наведи камеру на шарики и нажми кнопку "Сделать фото"',
            delay: 5450,
            emoji: 'max_1',
            next_status: 'scan_baloons'
          }
        ],
        'scan_models_dlg1': [
          {
            text: 'Верно, умничка)',
            delay: 1350,
            emoji: 'max_6',
          },
          {
            text: 'Слово это - подсказка тебе. Предлагаю использовать камеру и найти праздничное лакомство.',
            delay: 7400,
            emoji: 'max_7',
            next_status: 'scan_models'
          }
        ],
        'scan_models_dlg2': [
          {
            text: 'Хм, похоже это оно. Изучи внимательно, возможно ты что то найдешь...',
            delay: 5800,
            emoji: 'max_2',
            next_status: 'strawberry'
          }
        ],
        'strawberry_found': [
          {
            text: 'Ура, ты нашла вкусную клубничку!',
            delay: 2870,
            emoji: 'max_8'
          },
          {
            text: 'Хочешь пойти дальше?) ',
            delay: 1980,
            emoji: 'max_1'
          },
          {
            text: 'Знаю, что хочешь)',
            delay: 1530,
            emoji: 'max_5'
          },
          {
            text: 'Итак, далее ты отправляешься в свободное плавание. Дам тебе небольшую подсказку - смотри под...',
            delay: 8000,
            emoji: 'max_7',
            next_status: 'end'
          },
        ]
      },
    }
  },
  watch: {
    status() {
      this.startDialog();
    }
  },
  methods: {
    getPngUrl(img) {
      if (img == '') return null;

      const images = require.context('../assets/', false, /\.png$/);
      return images('./' + img + ".png");
    },
    scanBaloonsComplete() {
      this.status = 'scan_models_dlg1';
      this.startSceneListener();
    },
    listenerFunc(e) {
      const elem_id = e.target.getAttribute('id');
        this.t = elem_id;
        if (elem_id == 'cake') {
          if (this.status == 'scan_models') {
            this.status = 'scan_models_dlg2';
          }
        }
        if (elem_id == 'strawberry') {
          if (this.status == 'strawberry') {
            this.status = 'strawberry_found';
          }
        }
    },
    startSceneListener() {
      this.$el.addEventListener("markerFound", this.listenerFunc);
    },
    endSceneListener() {
      this.$el.removeEventListener("markerFound", this.listenerFunc);
    },
    timeout_msg(delay) {
      return new Promise(resolve => setTimeout(resolve, delay));
    },
    timeout_click(signal) {
      return new Promise((resolve, reject)=> {
        const error = new DOMException('aborted!', 'AbortError');

        const timeout = setTimeout(() => {
          resolve('you report!'); 
        }, 600000);

        if (signal.aborted) {
          clearTimeout(timeout);
          return reject(error);
        }

        signal.addEventListener('abort',() => {
          clearTimeout(timeout);
          reject(error);
        });
      });
    },
    async startDialog() {
      const messages = this.status_msgs[this.status];
      if (messages == undefined) {
        this.current_msg = '';
        return;
      }

      const msg_elem = document.querySelector('#msg');

      for (let i = 0; i < messages.length; i++) {
        const msg = messages[i];
        msg_elem.removeEventListener('click', stop);
        this.current_msg = msg.text;
        this.current_emoji = msg.emoji;
        this.current_delay = msg.delay;

        await this.timeout_msg(msg.delay);

        console.log('добавлен')
        let acontroller = new AbortController();
        let signal = acontroller.signal;
        msg_elem.addEventListener('click', stop);
        msg_elem.acontroller = acontroller;

        try {
          await this.timeout_click(signal);
        } catch (err) {
          // console.log();
        }

        if (i == messages.length - 1) {
          this.current_msg = '';
        }

        // eslint-disable-next-line no-prototype-builtins
        if (msg.hasOwnProperty('next_status')) {
          if (msg.next_status == 'end') {
            document.getElementById('arjs-video').style.display = 'none';
          }
          this.status = msg.next_status;
        }
      }
    },
  },
  async mounted() {
    this.startSceneListener();
    await navigator.mediaDevices.getUserMedia({ 
      video: {
        facingMode: 'environment'
      } 
    });
    this.startDialog();
  }
}
</script>

<style>
.animation_end {
  animation: 2s linear end;
}

@keyframes end {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

</style>